/*  Layout  JE  */

.header {
  width: 100%;
  height: 170px;
  background-color: #474646;
  text-align: center;
  position: fixed;
  z-index: 50;
  top: 0;
	transition: 0.5s;
}

section section {
  width: 100%;
  height: calc(100vh - 125px);
  background-color: white; 
  margin-block-end: 0px;
  box-sizing: border-box;
  padding: 10px;
}

section section:first-of-type {
  margin-top: 170px;
  height: calc(100vh - 170px);
  background-color: white;
}

footer {
  width: 100%;
  height: 200px;
  background-color: #ccc;
}

/*  Header  JE  */

.headerController {
  width: 100%;
	height: 40px;
	border: none;
}

.header a {
	display: block;
	float: right;
	line-height: 20px;
	margin-top: 5px;
	color: white;
	text-decoration: none;
  font-weight: 500;
  font-size: 1em;
  box-sizing: border-box;
  padding: 5px;
}

.header a:first-of-type {
  margin-right: 15%;
}

.header a:hover {
	text-decoration: underline;
}

.header h1 {
  font-size: 2em;
  margin-block-start: 0px; 
  margin-block-end: 0px;
  position: relative;
  color: white;
  text-shadow: 1px 2px black;
  transition: 0.5s;
}

.h1Mover {
  font-size: 1.8em !important;
}

.headShrinker {
	height: 130px;
}

.NavigationBar {
  width: 70%;
  float: left;
  position: absolute;
  bottom: 20px;
  margin-left: 15%;
}

.NavigationBar a {
  width: 25%;
  float: left;
  margin: 0px !important;
}

.NavigationBar > .active {
  /*
   * border-bottom: 1px solid white;
   */
   box-shadow: 0 2px 2px -2px white;

}

