/*  Log & Reg Form  JE  */

.cover {
  width: 100%;
  height: 100%;
  position: fixed;
  background-color: rgba(0,0,0,0.7);
  z-index: 100;
  top: 0;
}

.cover form {
  width: 40%;
  height: 50%;
  background-color: rgba(255, 249, 249, 1);
  display: block;
  margin: auto;
  position: relative;
  top: 50%;
  -webkit-transform: translateY(-50%);
  -ms-transform: translateY(-50%);
  transform: translateY(-50%);
  box-shadow: 0px 2px 5px 0px black;
}

.cover form h3 {
  width: 100%;
  float: left;
  font-size: 2.4em;
  text-align: center;
  margin-bottom: 30px;
}

.cover form div {
  width: 70%;
  height: 40px;
  margin-left: 15%;
  float: left;
  margin-bottom: 15px;
  box-shadow: 0 2px 2px -2px grey;
}

.cover form div:focus-within {
  box-shadow: 0 3px 2px -2px black;
}

.cover form div img {
  height: 30px;
  float: left;
  margin: 5px 0px 0px 10px;
}

.cover form div input {
  width: 85%;
  height: 40px;
  float: right;
  border: none;
  background-color: transparent;
}

.cover form div input:focus {
  outline: none;
}

.cover form button {
  width: 30%;
  height: 40px;
  float: right;
  margin-right: 35%;
  background-color: transparent;
  border: none;
  box-shadow: 0 2px 2px -2px grey;
}

.cover form a {
  height: 20px;
  width: 20px;
  position: static;
  float: right;
  margin: 5px 5px 0px 0px;
  color: black;
  text-align: center;
  text-decoration: none;
  line-height: 19px;
}

.cover form a:hover {
  background-color: rgba(0,0,0,0.7);
  border-radius: 25px;
  color: white;
}

.cover form footer {
  width: 100%;
  height: 50px;
  background-color: gray;
  position: absolute;
  bottom: 0;
}

.cover form footer a {
  width: 100%;
  float: left;
  height: 60px;
  text-align: center;
  line-height: 40px;
}

.cover form footer a:hover {
  background-color: transparent !important;
}

.RegisterForm {
  height: 70% !important;
}

.RegisterForm div input {
  width: 100% !important  ;
}

.RegisterForm div:first-of-type input {
  width: 50% !important; 
}